import jsons from '../../static/json/items.json';
export default {
	data() {
		return {
			arr: []
		};
	},
	created() {
		this.arr = jsons.item;
	},
	methods: {
		joincar(id) {
			this.$store.dispatch('joinstates', id);
		},
		toitem(_id) {
			console.log(_id);
			uni.navigateTo({
				url: '../item/item?id=' + _id
			});
		}
	}
};
